.button {

    background: $dark-gray;
    color: white;
    border: none;
    padding: $s-size;
    font-weight: 300;
    font-size: $font-size-large;
    display: inline-block;
    text-decoration: none;
    line-height: 1;


}

.button--link {
    background: none;
}

.button--secondary {
    background-color: #888;
}